.pagePortfolios {
    // padding: 3vh 2vw;

    &__content {
        min-height: 100%;
        padding: 20px 1.5%;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 20px;

        .pageHeadline {
            margin-bottom: 8vh;
        }

        &_workLinksContainer {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 60px;

            li {
                max-width: 100%;
            }
        }
    }
}